/*! fabian.buechler.io */

/* Normalize */

@import '../node_modules/normalize.css/normalize.css';

@import 'colors_fonts';
@import 'icons';
@import 'basic';

/* Basic Layout. */

html {
    height: 100%;
}

body {
    height: 100%;
    margin: 0;
}

/* Highlighted Headings. */
.highlight {
    background-color: $green-emerald;
    color: $white-egshell;
    display: inline-block;
    line-height: 1;
    padding: 0.3rem;
}

/* Site Header. */

.site-header {
    background-color: $gray-pastel;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 1.5rem repeat(3, auto);
    grid-template-areas:
        "x avatar avatar avatar avatar language"
        "x avatar avatar avatar avatar y"
        "header header header header header header"
        "tagline tagline tagline tagline tagline tagline";
    grid-row-gap: 0.5rem;
    padding: 1rem;
    text-align: center;

    h1 {
        grid-area: header;
        margin: 0;
    }
}


.language {
    grid-area: language;
    text-align: right;

    ul {
        list-style: none;
        margin: 0;
        padding-left: 0;
    }

    li {
        display: inline-block;
    }

    abbr {
        text-decoration: none;
    }
}


.tagline {
    color: $green-midnight;
    font-weight: 700;
    grid-area: tagline;
    margin-bottom: 0;
}

.avatar {
    display: block;
    grid-area: avatar;
    margin: 0 auto;
    max-height: 8rem;
    max-width: 8rem;
    border-radius: 50%;
    box-shadow:
        0 0 0 0.2rem hsl(133, 47%, 68%), /* Eton Blue inner border */
        0 0 0 0.3rem hsl(195, 70%, 22%), /* Midnight green outer border */
        0 0.1rem 0.2rem 0.3rem hsla(195, 70%, 22%, 0.7); /* Midnight green drop shadow */
}

/* Main Layout. */

main {
    padding: 1rem;
}

.cv header {
    color: $green-midnight;
    display: block;
    margin-bottom: 3rem;
    text-align: center;
}

footer {
    background-color: $green-midnight;
    color: white;
    padding: 1rem;

    .contact {
        ul {
            display: grid;
            grid-template-rows: repeat(4, minmax(2rem, max-content));
            grid-template-areas:
            "email"
            "twitter"
            "linkedin"
            "xing";
            grid-gap: 0.7rem;
            list-style: none;
            padding-left: 0;
        }

        .email {
            grid-area: email;
        }
        .twitter {
            grid-area: twitter;
        }
        .linkedin {
            grid-area: linkedin;
        }
        .xing {
            grid-area: xing;
        }
    }
}



.license {
    font-size: 0.7rem;

    img {
        vertical-align: middle;
    }
}


/* All Sections. */

.cv-section {
    display: grid;
    margin-bottom: 1rem;

    & > ol,
    & > ul {
        list-style: none;
        padding-left: 0;
    }

    li {
        margin-bottom: 1rem;

        & > ul {
            padding-left: 1rem;
        }
    }

    /* Section Skills. */

    &.skills {
        .content {
            display: grid;
            grid-template-columns: 1fr 3fr;
            grid-auto-rows: minmax(2rem, max-content);
            grid-column-gap: 0.5rem;
            grid-row-gap: 1rem;
        }

        h3 {
            margin-bottom: 0;
        }

        ul {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-auto-rows: minmax(1rem, max-content);
            grid-column-gap: 0.5rem;
            grid-row-gap: 0;
            list-style: none;
            margin: 0;
        }

        li {
            margin-bottom: 0.5rem;
        }
    }
}

.date-range {
    background-color: $green-pastel;
    color: white;
    font-weight: 400;
    padding: 0 0.3rem;

    &:after {
        content: "\A";
        white-space: pre;
    }
}



/* Index Page. */

.index,
.index footer {
    height: 100%;
}


/* Screens > 520 width. */

@media screen and (min-width: 520px) {

    /* Section Skills: three columns: */
    .cv-section.skills ul {
        grid-template-columns: repeat(3, 1fr);
    }

}

@media screen and (min-width: 520px) and (max-width: 1199px) {

    /* Footer Social Icons to the right. */
    footer .contact ul {
        grid-template-columns: 2fr 1fr;
        grid-template-rows: repeat(3, minmax(2rem, max-content));
        grid-template-areas:
            "email twitter"
            "email linkedin"
            "email xing";
    }

}

/* Screens > 768 width. */

@media screen and (min-width: 768px) and (max-width: 1199px) {

    /* Horizontal side header. */
    .site-header {
        grid-template-columns: 1fr 10rem 3fr 1fr;
        grid-template-rows: 1rem 3rem 3rem 1rem;
        grid-template-areas:
            "x avatar w       language"
            "x avatar header  y"
            "x avatar tagline y"
            "x avatar z       y";
        grid-column-gap: 2rem;
        text-align: left;
    }

    .avatar {
        max-height: 9rem;
        max-width: 9rem;
    }

}

@media screen and (min-width: 768px) {

    /* Sections in two columns. */

    main .cv {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: repeat(6, auto);
        grid-template-areas:
            "header header"
            "about edu"
            "personal edu"
            "exp exp"
            "skills skills"
            "hobby hobby";
        grid-column-gap: 1rem;
    }

    .cv header {
        grid-area: header;
    }
    .cv .about {
        grid-area: about;
    }
    .cv .edu {
        grid-area: edu;
    }
    .cv .personal {
        grid-area: personal;
    }
    .cv .exp {
        grid-area: exp;
    }
    .cv .skills {
        grid-area: skills;
    }
    .cv .hobby {
        grid-area: hobby;
    }

    /* Section Skills: multiple columns: */
    .cv-section.skills ul {
        grid-template-columns: repeat(3, 1fr);
    }

}

/* Screens > 1200 width. */

@media screen and (min-width: 1200px) {

    /* Horizontal Header > Main > Footer. */

    body {
        display: grid;
        grid-template-columns: 3fr 7fr 2fr;
        grid-template-rows: auto;
        grid-template-areas: "header main footer";
    }

    /* No Main on Index page. */
    .index {
        grid-template-columns: 1fr 2fr;
        grid-template-rows: auto;
        grid-template-areas: "header footer";
    }

    .site-header {
        grid-area: header;
        grid-template-rows: repeat(3, auto) 1fr;
        grid-template-areas:
            "x avatar avatar avatar avatar language"
            "x avatar avatar avatar avatar y"
            "header header header header header header"
            "tagline tagline tagline tagline tagline tagline"
            "z z z z z z";
    }

    main {
        grid-area: main;
    }

    footer {
        grid-area: footer;
    }

    /* Larger Avatar. */
    .avatar {
        max-height: 12rem;
        max-width: 12rem;
    }

}

/* Print styles. */

@media print {

    @page {
        size: A4;
    }

    /* Smaller text. */
    body {
        font-size: 0.75rem;
    }

    /* No Header background. */
    .site-header {
        background-color: transparent;
    }

    /* Hide footer and language navigation. */
    footer, .language {
        display: none;
    }

    h1, h2, h3, h4, h5 {
        page-break-after: avoid;
    }

    /* Add a.href to text in brackets. */
    a.xref:after {
        content: " (" attr(href) ")";
    }

    /* No grid display for Sections, cut-off text on page breaks. */
    .cv-section {
        display: block;
    }

    /* No background for Section headers and dates. */

    .highlight {
        background-color: transparent;
        color: $green-emerald;
        padding: 0;
    }

    .date-range {
        background-color: transparent;
        color: $green-pastel;
        padding: 0;
    }

    /* Section Skills: four columns, less margin. */
    .cv-section.skills ul {
        grid-template-columns: repeat(4, 1fr);
        grid-row-gap: 0;
    }

    .cv-section.skills .content {
        page-break-inside: avoid;
    }

}



